/** select customize style to set background as transparent */
const cellSelectStyles = makeStyles({
	select: {
		backgroundColor: 'transparent',
	},
});

/** customization of material ui select for the page */
const Select = ({ id, value, items, onChange }: any) => {
	const cellSelectClasses = cellSelectStyles();
	return (
		<MaterialUI.Select
			id={id}
			classes={cellSelectClasses}
			value={value}
			onChange={onChange}
			style={{ backgroundColor: 'transparent', color: '#242C2E', fontSize: '0.875em' }}
			renderValue={(selected: any) => {
				if (selected != null) {
					const value: any = items.find((item: any) => {
						return item.value == selected;
					});
					if (value) {
						return value.name;
					} else {
						return '—';
					}
				}
				return '—';
			}}
		>
			{items.map((item: any) => (
				<MaterialUI.MenuItem key={item.value} value={item.value}>
					{item.name}
				</MaterialUI.MenuItem>
			))}
		</MaterialUI.Select>
	);
};

